.App {
  font-family: Arial, Helvetica, sans-serif;
  /* background-color: #333; */
  text-align: center;
  /* overflow: auto; */
}

.App input{
  display: block;
  margin: 0 auto;
  height: 11vh;
  width: 100%;
  font-size: 20px;
  font-size: 3vh;
  padding: 20px 48px;
  outline: none;
  border: none;
  border-radius: 10px;
  box-shadow: 0 4px 4px 0 rgba(0,0,0,0.1);
}

.input {
  border-radius: 10px;
  background: white;
  position: absolute;
  width: 34%;
  right: 33%;
  top: 30%;
  z-index: 100;
  /* transform: translateY(-50%); */
}

.input i{
  position: absolute;
  top: 38%;
  right: 91%;
 
}

.title {
  font-family: 'Bebas Neue', cursive;
  color: #333;
  margin-top: 5vh;
  padding: 10px;
  font-size: 13vh;
}

/* header and search bar */
.head {
  display: flex;
  width: 100%;
  height: 35vh;
  background-color: #95BF47;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* align-items: center; */
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  
  margin-bottom: 2vh;
}


/* body */
.body {
  display: flex;
  background-color: #333;
  /* border: 1px solid black; */
  justify-content: space-around;
  flex-direction: row;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  /* background-color: #EEEEEE; */
}

/* nomination div */
.list-header {
  display: table;
  /* overflow-wrap: inherit; */
  margin-top: 8vh;
  margin-bottom: 5vh;
  padding: 1vh;
  width: 70vh;
  height: 70vh;
  z-index: 0;
  position: sticky;
  top: 2.5vh;
  background-color: #F2F3F2;
  border-radius: 3px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-color: black;
}

.nominee-header {
  padding-top: 2vh;
}

.nominee {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 65vh;
  height: 15vh;
  margin: 2vh;
  text-align: center;
  padding: 3vh;
  
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.original-nomineee {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.original {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 8vh;
  margin-bottom: 5vh;
  width: 130vh;
  height: 70vh;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  background-color: #F2F3F2;
  border-radius: 3px;
}


.content {
  margin-top: 8vh;
  margin-bottom: 5vh;
  width: 130vh;
  display: flex;
  background-color: #F2F3F2;
  /* align-items: stretch; */
  flex-wrap: wrap;
  padding: 4vh;
  justify-content: space-between;
  border-radius: 3px;
  /* justify-content: space-evenly; */
  /* justify-content: left; */
  /* box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); */
}

.movie {
  max-width: 200px;
}

.nominee-title {
  flex-grow: 0;
  margin: 0;
  padding-right: 2vh;
}
.nominee-year {
  margin: 0;
  flex-grow: 0;
}
.delete {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    flex-grow: 3;
    border:0;
    background:none;
}

.delete i:hover {
    color: #F26C6C;
}



footer {
  
  background-color: #131212;
	height: 10vh;
  padding-top: 3.3vh;
	text-align: center;
  color: white;
  font-family: 'Open Sans', sans-serif;
}

.foot-text {
  top: 50%;
  font-size: 2.4vh;
}